@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');
@import './colours';

* {
    margin: 0;
    padding: 0;
  }
  
  html, body {
    height: 100%;
  }
  
  html {
    background: linear-gradient($background-dark-gradient, $background-light-gradient);
    font-size: 10px;
  }
  
  body {
    display: flex;
    font-size: 1.8rem;
    font-family: 'Poppins', sans-serif;
    font-weight: 300;
  }
  
  #root {
    display: grid;
    place-items: center center;
    flex: 1;
  }
